div.index {
    display: grid;
    grid-template-columns: 100vw;
    grid-template-rows: repeat(7);
    gap: 1px 1px 1px 1px ;
    grid-template-areas: "navbar" "main-text" "authorName" "sub-text" "image" "button" "scroll";
    justify-content: center;
    align-items: center;
    font-family: Roboto;
}

div.main-text { 
    grid-area: main-text;
    margin-top: 1rem;
	font-weight: 300;
	font-size: 35px;
	display: flex;
	justify-content: center;
	align-items: center; 
    
}

div.authorName { 
    grid-area: authorName;
    font-weight: 900;
	font-size: 60px;	
	letter-spacing: -4px;
	display: flex;
	justify-content: center;
	align-items: center;
}

div.sub-text { 
    grid-area: sub-text; 
	padding: 0rem 2rem;
	font-weight: 300;
	font-size: 20px;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
}

img.image { 
    grid-area: image; 
    display: block;
	margin: 0 auto;
	margin-top: 3rem;
	width: 200px;
	height: 200px;
	border-radius: 100px;
}

a.button { 
    grid-area: button; 
    display: block;
	margin: 0 auto;
	margin-top: 2rem;
	width: 163px;
	height: 50px;
	background: url(../../assets/images/downloadButton.svg);
	transition: background 0.3s;
}

a.button:hover { 
    /* grid-area: button;  */
    background: url(../../assets/images/downloadButtonBlue.svg);
	cursor: pointer;
}

p.scroll { 
    font-family: Roboto;
	padding: 2rem;
	font-weight: 300;
	font-size: 20px;
	text-align: center;
	display: flex;
	justify-content: center;
	align-items: center;
	
}

/* Adapting to screens with max height of 600px */
@media (max-height: 600px) {
	p.scroll {
		margin-top: -1rem;
	}
}

/* Adapting to screens with max height of 700px */
@media (max-height: 700px) {

	p.scroll { /* Alters "Scroll down" text */
		font-family: Roboto;
		font-weight: 300;
		font-size: 20px;
		text-align: center;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	
	div.main-text {
		font-weight: 300;
		font-size: 25px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	
	div.authorName {	
		font-weight: 900;
		font-size: 50px;	
		letter-spacing: -4px;
		display: flex;
		justify-content: center;
		align-items: center;
	}
	
	div.sub-text {
		padding: 0rem 2rem;
		font-weight: 300;
		font-size: 18px;
		text-align: center;
		display: flex;
		justify-content: center;
		align-items: center;
	}

	img.image {
		display: block;
        margin: 0 auto;
        margin-top: 1rem;
		width: 150px;
		height: 150px;
		border-radius: 100px;
	}
}

/* Adapting to screens with min width of 700px */
@media (min-width: 700px) {

    div.index {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: 0.1fr 0fr 0fr 0fr 0.1fr;
        grid-template-areas: "navbar ." "main-text image" "authorName image" "sub-text image" "button .";
        font-family: Roboto;
    }
    
    div.main-text { 
        grid-area: main-text;
        margin-top: -0.7rem;
		font-weight: 400;
		font-size: 50px;
		display: flex;
        justify-content: flex-start;
        align-content: center;
		margin-left: 5rem;
    }
    
    div.authorName { 
        grid-area: authorName;
        font-weight: 900;
		font-size: 100px;	
		/* margin-top: -10rem; */
		letter-spacing: -4px;
		display: flex;
		justify-content: flex-start;
		margin-left: 5rem;
    }
    
    div.sub-text { 
        grid-area: sub-text; 
		font-weight: 300;
		font-size: 30px;
		text-align: justify;
		display: flex;
		justify-content: flex-start;
		margin-left: 3.2rem;		
		width: 500px;
        
    }
    
    img.image { 
        grid-area: image; 
        display: flex;
        justify-content: center;
        align-items: center;
        width: 400px;
        height: 400px;
        border-radius: 0px;
        box-shadow: 70px 50px 1px 1px #58656D;
        margin-top: 7rem;
    }
    
    a.button { 
        grid-area: button;
        display: flex;
		justify-content: flex-start;
		margin-left: 5rem;
		width: 163px;
		height: 50px;
		background: url(../../assets/images/downloadButton.svg);
		transition: background 0.3s;
        
    }
    
    p.scroll { 
        display: none;
    }

}